.lolomo__row__carousel {
  height: 125px;
  width: 100%;
  position: relative;
}

.carousel__nav {
  display: flex;
  position: absolute;
  top: -20px;
  right: 70px;
}

.carousel__indicator {
  margin-left: 10px;
  height: 5px;
  width: 25px;
  background-color: grey;
}

.carousel__nav .current {
  background-color: white;
}

.carousel__button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  height: 100%;
  width: 50px;
  border: none;
  font-size: 25px;
  color: white;
  background-color: rgba(0, 0, 0, 0.562);
  cursor: pointer;
}

.carousel__button--left {
  left: -50px;
}

.carousel__button--right {
  right: 0px;
}

.carousel__track {
  display: flex;
  height: 100%;
  width: 100%;
  overflow: hidden;
  list-style: none;
}

.carousel__slide {
  height: 100%;
  min-width: 220px;
  margin-left: 10px;
  background-color: lightcoral;
}

.carousel__slide:first-of-type {
  margin: 0;
}

.carousel__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 5px;
}
